.whats-new-rss-flyout {
	font-family: Inter, sans-serif !important;

	.whats-new-rss-flyout-contents {

		top: 32px !important;
		height: calc(100% - 32px) !important;

		.whats-new-rss-flyout-inner-header {
			padding: 20px !important;
		}

		.whats-new-rss-flyout-inner-content {

			.new-post-badge {
				display: none;
			}

			.whats-new-rss-flyout-inner-content-item {
				.rss-content-header {
					h2 {
						font-weight: 600 !important;
					}
				}

				a {

					&:active,
					&:focus {
						box-shadow: none;
					}
				}
			}
		}
	}
}

@media screen and (max-width: 782px) {
	.whats-new-rss-flyout .whats-new-rss-flyout-contents {
		top: 46px !important;
	}
}